import inputCell from '@/components/input-cell'
import { verifyPhone, dealCountDown } from '@/common/js/common'
import { mapState, mapActions } from 'vuex'
import { changePhone } from '@/api'
export default {
	name: '',
	data () {
		return {
			count: 0,
			phone: '',
			code: ''
		}
	},
	onLoad () {

	},
	methods: {
		...mapActions(['reqGetSms']),
		onGetSmsEvent () {
			let self = this;
			if(!verifyPhone(this.phone)) {
				return;
			}
			this.reqGetSms({
				mobile: this.phone,
				callBack: dealCountDown,
				self: self
			})
		},
		async onConfirmClick () {
			if(!verifyPhone(this.phone)) {
				return;
			}
			if(this.code == '') {
				uni.showToast({
					title: '请输入验证码',
					icon: 'none'
				})
				return;
			}
			let result = await changePhone ({
				token: uni.getStorageSync('token'),
				mobile: this.phone,
				captcha: this.code
			})
			if(result.code == 200) {
				uni.showModal({
					title: '修改手机',
					content: '修改成功',
					showCancel: false,
					success: function (res) {
						if(res.confirm) {
							uni.navigateBack()
						}
					}
				})
			}	
		}
	},
	components: {
		inputCell
	}
}
